import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import './App.css';
import { ethers } from 'ethers';

function Home() {
  const [selectedArtwork, setSelectedArtwork] = useState(null);
  const [isRatingOpen, setIsRatingOpen] = useState(false);
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);
  const [unreadCount] = useState(0);
  const [artworks, setArtworks] = useState([]);

  useEffect(() => {
    const fetchArtworks = async () => {
      try {
        const response = await fetch('http://localhost:8000/api/artworks/');
        const data = await response.json();
        setArtworks(data);
      } catch (error) {
        console.error('Failed to fetch artworks:', error);
      }
    };

    fetchArtworks();
  }, []);

  const toggleSidebar = () => setIsSidebarOpen(!isSidebarOpen);
  const handleLogout = () => setIsRatingOpen(true);

  const handleRatingSubmit = (rating) => {
    console.log(`Rating submitted: ${rating}`);
    window.location.href = '/logout';
  };

  const handleSelectArtwork = (artwork) => {
    setSelectedArtwork(artwork);
  };

  return (
    <div className="min-h-screen bg-gray-50 font-sans">
      <Header toggleSidebar={toggleSidebar} />
      <Sidebar isOpen={isSidebarOpen} toggleSidebar={toggleSidebar} unreadCount={unreadCount} onLogout={handleLogout} />

      <ArtworkGallery artworks={artworks} onSelectArtwork={handleSelectArtwork} />

      {selectedArtwork && <ArtworkModal artwork={selectedArtwork} onClose={() => setSelectedArtwork(null)} />}

      {isRatingOpen && (
        <RatingModal onSubmit={handleRatingSubmit} onClose={() => setIsRatingOpen(false)} />
      )}
    </div>
  );
}

function Header({ toggleSidebar }) {
  return (
    <div className="header-container">
      <button className="menu-btn" onClick={toggleSidebar}></button>
      <header>
        <div className="logo">Controlled. Motives</div>
        <nav>
          <button className="search-btn">
            <svg xmlns="http://www.w3.org/2000/svg" width="17" height="15" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
              <circle cx="11" cy="11" r="8"></circle>
              <line x1="21" y1="26" x2="16.65" y2="16.75"></line>
            </svg>
          </button>
          <Link to="/"><i className="fas fa-home"></i> Home</Link>
          <Link to="/gallery"><i className="fas fa-images"></i> Gallery</Link>
          <Link to="/artist"><i className="fas fa-paint-brush"></i> Artist</Link>
        </nav>
      </header>
    </div>
  );
}

function Sidebar({ isOpen, toggleSidebar, unreadCount, onLogout }) {
  return (
    <div className={`sidebar ${isOpen ? 'expanded' : ''}`}>
      <button className="close-btn" onClick={toggleSidebar}></button>
      <div className="menu-items">
        <Link to="/profile"><i className="fas fa-user icon"></i><span className="link-text">Profile</span></Link>
        <Link to="/"><i className="fas fa-home icon"></i><span className="link-text">Home</span></Link>
        <Link to="/gallery"><i className="fas fa-images icon"></i><span className="link-text">Gallery</span></Link>
        <Link to="/artist"><i className="fas fa-paint-brush icon"></i><span className="link-text">Artist</span></Link>
        <Link to="/moments"><i className="fas fa-film icon"></i><span className="link-text">Moments</span></Link>
        <Link to="/transactions"><i className="fas fa-exchange-alt icon"></i><span className="link-text">Transactions</span></Link>
        <Link to="/send"><i className="fas fa-paper-plane icon"></i><span className="link-text">Send</span></Link>
        <Link to="/about"><i className="fas fa-info-circle icon"></i><span className="link-text">About</span></Link>
        <Link to="/notifications">
          <i className="fas fa-bell icon"></i>
          <span className="link-text">Notifications</span>
          {unreadCount > 0 && <span className="notification-badge">{unreadCount}</span>}
        </Link>
        <button onClick={(e) => { e.preventDefault(); onLogout(); }} style={{ color: 'red', background: 'none', border: 'none', cursor: 'pointer' }}>
          <i className="fas fa-sign-out-alt icon"></i><span className="link-text">Logout</span>
        </button>
      </div>
    </div>
  );
}

function ArtworkGallery({ artworks, onSelectArtwork }) {
  return (
    <section className="section">
      <h2>Trending Artworks</h2>
      <div className="artwork-gallery">
        {artworks.length > 0 ? (
          artworks.map((artwork) => (
            <ArtworkCard key={artwork.id} artwork={artwork} onSelectArtwork={onSelectArtwork} />
          ))
        ) : (
          <p>No trending artworks available.</p>
        )}
      </div>
    </section>
  );
}

function ArtworkCard({ artwork, onSelectArtwork }) {
  const [isFlowered, setIsFlowered] = useState(artwork.floweredByUser);
  const [flowerCount, setFlowerCount] = useState(artwork.total_flowers);
  const navigate = useNavigate();

  const handleFlowerToggle = (e) => {
    e.stopPropagation();
    const newFlowered = !isFlowered;
    setIsFlowered(newFlowered);
    setFlowerCount(newFlowered ? flowerCount + 1 : flowerCount - 1);
  };

  const handleBuy = (e) => {
    e.stopPropagation();
    navigate(`/checkout/${artwork.id}`);
  };

  return (
    <div className="artwork-card" onClick={() => onSelectArtwork(artwork)}>
      <img src={artwork.image.url} alt={artwork.title} />
      <div className="artwork-details">
        <div className="buttons-container">
          <button className="flower-button" onClick={handleFlowerToggle}>
            {isFlowered ? '' : ''}
          </button>
          <span>{flowerCount}</span>
          <button className="buy-btn" onClick={handleBuy}>
            
          </button>
          <span>ETH {artwork.price}</span>
        </div>
        <div className="artwork-info">
          <h3 className="artwork-title">{artwork.title}</h3>
          <p className="artwork-description">{artwork.description.slice(0, 100)}...</p>
          <div className="artwork-meta">
            <span className="meta-item">
              <i className="fas fa-calendar-alt"></i> {new Date(artwork.created_at).toLocaleDateString()}
            </span>
            <span className="meta-item">
              <i className="fas fa-flower-tulip"></i> {flowerCount}
            </span>
            {artwork.price && (
              <span className="meta-item">
                <i className="fas fa-dollar-sign"></i> {artwork.price}
              </span>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

function ArtworkModal({ artwork, onClose }) {
  const handleBuyNFT = async () => {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const provider = new ethers.providers.Web3Provider(window.ethereum);
        const signer = provider.getSigner();
        const transaction = {
          to: artwork.blockchain_address,
          value: ethers.utils.parseEther(artwork.price),
        };
        const tx = await signer.sendTransaction(transaction);
        console.log('Transaction sent:', tx);
        alert(`Transaction sent: ${tx.hash}`);
      } catch (err) {
        console.error('Error sending transaction:', err);
      }
    } else {
      alert("Ethereum wallet is not connected.");
    }
  };

  return (
    <div className="artwork-modal">
      <div className="modal-content">
        <button className="close-btn" onClick={onClose}></button>
        <img src={artwork.image.url} alt={artwork.title} />
        <h2>{artwork.title}</h2>
        <p>{artwork.description}</p>
        <div className="modal-footer">
          <span>{artwork.price} ETH</span>
          <button onClick={handleBuyNFT}>Buy NFT</button>
        </div>
      </div>
    </div>
  );
}

function RatingModal({ onSubmit, onClose }) {
  return (
    <div className="modal">
      <div className="modal-content">
        <h3>Rate Your Experience</h3>
        <div>
          {[1, 2, 3, 4, 5].map((num) => (
            <button key={num} onClick={() => onSubmit(num)}>{num}</button>
          ))}
        </div>
        <button onClick={onClose}>Close</button>
      </div>
    </div>
  );
}

export default Home;